import Vue from "vue";
import VueRouter from "vue-router";
import no2 from "../views/no2.vue";
import no1 from "../views/no1.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "no2",
    component: no2
  },
  {
    path: "/no1",
    name: "no1",
    component: no1
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

export default router;
